// Structure

$base__space-unit: 1.5rem;

// Colors

$purple: #ba0e82;
$green: #a1b426;
$blue: #00a3d7;
$orange: #f7911d;
$grey: #4a4a4a;

$color__background-body: #fff;
$color__text-main: #000000;

$color__background-screen: #f1f1f1;
